import React from "react";
import ReactDOM from "react-dom";
import loadable from "@loadable/component";

import Loading from './components/Loading';

import configMap from "Utils/GetGlobals";
import SetConfigMap from "Utils/SetConfigMap";
import log from "Log";

const LoadableFacebookComponent = loadable(() => import("./blocks/Facebook"), {
  fallback: <Loading/>
});

window.facebook_posts = (function() {
  const configModule = function(input_map) {
    SetConfigMap({
      input_map: input_map,
      settable_map: configMap.settable_map,
      config_map: configMap
    });
  };

  const initModule = function() {
    ReactDOM.render(
      <LoadableFacebookComponent />,
      document.querySelector("#facebook-output-container")
    );

    log("It is running, jap", true);
  };

  return {
    configModule: configModule,
    initModule: initModule
  };
})();
